@import '../../assets/css/variable.less';

.trade {
  margin: 0 24px;
}

.trade-layout {
  display: grid; 
  grid-template-columns: 404px auto auto;
  grid-template-rows: 96px 400px auto auto;
  grid-template-areas: "symbol-area trade-info trade-info" "sidebar kline kline" "sidebar my-info my-info" "sidebar contract contract";
  gap: 0px;
  border-top: 1px solid #203B60;

  .sidebar {
    grid-area: sidebar;
    border-right: 1px solid #203B60;
  }
  .symbol-area {
    grid-area: symbol-area;
    border-right: 1px solid #203B60;
  }
  .trade-info {
    grid-area: trade-info;
    height: 96px;
  }

  .kline {
    grid-area: kline;
    border-top: 1px solid #203B60;
  }
  .my-info {
    grid-area: my-info;
    border-top: 1px solid #203B60;
    z-index: 1;
  }
  .contract {
    grid-area: contract;
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60
  }
  .trades {
    grid-area: trades;
    border-top: 1px solid #203B60;
  }
}

//小于375px
@media only screen and (min-width: 320px) {
  .trade-layout {
    grid-template-columns: 100%;
    grid-template-areas: "symbol-area" "trade-info" "sidebar" "my-info" "contract" ;
    grid-template-rows: 96px auto auto auto auto auto auto ;
  } 
}
//小于760 大于375px
@media only screen and (min-width : 375px) {
  .trade-layout {
    grid-template-columns: 100%;
    grid-template-areas: "symbol-area" "trade-info" "sidebar"  "my-info" "contract" ;
    grid-template-rows: 96px auto auto auto auto auto auto ;


    .kline {
      display: none;
    }

    .symbol-area {
      border-right: 0;
      border-top :1px solid #203B60 ;
      border-bottom: 1px solid #203B60;
    }
    .sidebar {
      border-right: 0;
    }
    .contract {
      border-right: 0;
    }
    .my-info, .trade-info{
      overflow: hidden;
    }
  }
}
//小于1140 大于760
@media only screen and (min-width : 760px) {
  .trade-layout {
    grid-template-columns: 404px auto auto;
    grid-template-rows: 96px 400px auto auto;
    grid-template-areas: "symbol-area trade-info trade-info" "sidebar kline kline" "sidebar my-info my-info" "sidebar contract contract";
    .symbol-area {
      border-right: 1px solid #203B60;
      border-top : 0;
      border-bottom: 0;
    }
    .kline {
      display: block;
    }
    .sidebar {
      border-right: 1px solid #203B60;
    }
    .contract {
      border-top: 1px solid #203B60;
      border-right: 1px solid #203B60
    }
    .trades {
      border-top: 1px solid #203B60;
    }
  }

}
